let imgData = null;
export function getAtlas(cb: (loadedImage: HTMLImageElement) => void) {
  if (imgData == null) {
    imgData = new Image();
    imgData.src = `data:image/png;base64,`;
    imgData.onload = () => imgData && cb(imgData);
  }
  return imgData;
}
